import { authAPI } from "../../API/Api";
import { stopSubmit } from "redux-form";
import { securityAPI } from './../../API/Api';

const SET_USER_DATA = 'SET-USER-DATA';
const GET_CAPTCHA_URL = 'GET-CAPTCHA-URL';

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    captchaUrl: null
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.data
            }
        case GET_CAPTCHA_URL:
            return {
                ...state,
                captchaUrl: action.captchaUrl
            }
        default:
            return state
    }
    return state
}

export const setAuthUserData = (userId, email, login, isAuth) => ({ type: SET_USER_DATA, data: { userId, email, login, isAuth } })
export const getCaptchaUrlSuccess = (captchaUrl) => ({ type: GET_CAPTCHA_URL, captchaUrl })


export const setAuthUser = () => (dispatch) => {
    return authAPI.getAuthUser().then(data => {
        if (data.resultCode === 0) {
            let { id, email, login } = data.data
            dispatch(setAuthUserData(id, email, login, true))
        }
    })
}

export const login = (email, password, rememberMe, captcha) => (dispatch) => {
    authAPI.login(email, password, rememberMe, captcha).then(response => {
        if (response.data.resultCode === 0) {
            dispatch(setAuthUser())
        } else {
            let errorMessage = response.data.messages.length > 0 ? response.data.messages[0] : 'Some error, ask Administrator'
            if (response.data.resultCode === 1) {
                dispatch(stopSubmit('login', { _error: errorMessage }))
            } else if (response.data.resultCode === 10) {
                dispatch(getCaptchaUrl())
                dispatch(stopSubmit('login', { _error: errorMessage }))
            }
        }
    })
}

export const getCaptchaUrl = () => async (dispatch) => {
    const response = await securityAPI.getCaptchaUrl()
    const captchaUrl = response.data.url

    dispatch(getCaptchaUrlSuccess(captchaUrl))
}

export const logout = () => (dispatch) => {
    authAPI.logout()
        .then(response => {
            if (response.data.resultCode === 0) {
                dispatch(setAuthUserData(null, null, null, false))
            }
        })
}
export default authReducer